import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';
import HelpIcon from '@mui/icons-material/Help';
import { Accordion, AccordionDetails, AccordionSummary, Box, Button, Grid, Link, Paper, Skeleton, styled, Tooltip, Typography, Alert } from "@mui/material";
import React, { useEffect, useState } from "react";
import { getNetworkManager } from '../../../../common/network';
import { getPoolService } from '../../../../services/pool.service';
import { getEmitter } from '../../../../util/emitter.util';
import { getLogger } from "../../../../util/logger";
import { SnackbarUtil } from '../../../../util/snackbar.util';
import { Pool, PoolDialogState, PoolDialogType } from "../../../../util/types";
import { ViteUtil } from "../../../../util/vite.util";
import { ClickOnceButton } from '../../../common/components/click-once-button';
import { PoolCountdown } from "../countdown";
import { PoolDialog } from "../dialog";
import { Rewards } from '../rewards';
//import { NetRewards } from '../netrewards';
import { Tokens } from "../tokens";
import BigNumber from 'bignumber.js';
import { ProgressBar } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';


const logger = getLogger()

const TransparentPaper = styled(Paper)(
  ({ theme }) => ({
    padding: theme.spacing(2),
    paddingTop: theme.spacing(1),
    backgroundColor: "transparent"
  }));

interface Props {
  account?: Maybe<string>
  pool?: Maybe<Pool>
}

export const PoolListItem: React.FC<Props> = (props: Props) => {
  const [dialogState, setDialogState] = useState<PoolDialogState>({
    type: PoolDialogType.DEPOSIT,
    open: false
  });
  const [canStake, setCanStake] = useState<boolean>(false);
  const [canClaim, setCanClaim] = useState<boolean>(false);
  const [canWithdraw, setCanWithdraw] = useState<boolean>(false);
  const [pool, setPool] = useState<Maybe<Pool>>(props.pool);
  const emitter = getEmitter();
  const poolService = getPoolService();
  const networkManager = getNetworkManager();
  const network = networkManager.getNetwork();
  const [newRewards, setNewRewards] = useState<BigNumber>(new BigNumber(0));
  const [lock, setLock] = useState<Number>(0);  


  useEffect(() => {
    if (props.pool) {
      logger.info(`Pool loaded: ${props.pool?.id}`)();
      setCanWithdraw(!!props.pool && !!props.account && (props.pool.userInfo?.stakingBalance.gt(0) ?? false));
    } else {
      setCanWithdraw(false);
    }

    if (props.pool?.lockTime !== undefined) {
      let lockTime = props.pool.lockTime.toNumber();
      if (lockTime > 0) {
        if (props.pool.id === 20 && network?.name === "MAINNET v0.4") {
          setLock(0);
        } else {
          lockTime = lockTime / 60 / 60 / 24;
          if (lockTime < 1) {
            setLock(0);
          } else {
            setLock(lockTime);
          }
        }
      } else {
        setLock(0);
      }
    } else {
      setLock(0);
    }
  }, [props.pool, props.account, network?.name]);

  useEffect(() => {
    let interval = setInterval(() => {
      //setCanStake(false); // quick fix to prevent more staking.
      const tempnetwork = networkManager.getNetwork();
	setCanStake(!!props.pool && !!props.account && !!tempnetwork && (tempnetwork.name !== "MAINNET v0.2") && !((tempnetwork.name === "MAINNET v0.4") && (props.pool.id === 85)) );
    }, 500);
    return () => {
      clearInterval(interval);
    }
  }, [props.pool, props.account, networkManager]);

  useEffect(() => {
    if (props.pool) {
      const height = networkManager.networkHeight;
      const newRewards = ViteUtil.calculateNetRewardTokens(pool, height);
      setNewRewards(newRewards);
    } else {
      setNewRewards(new BigNumber(0));
    }
  }, [pool, networkManager, props]);

  useEffect(() => {
    let interval = setInterval(() => {

      if (props.pool) {
        const height = networkManager.networkHeight;
        const newRewards = ViteUtil.calculateNetRewardTokens(pool, height);
        setNewRewards(newRewards);
      } else {
        setNewRewards(new BigNumber(0));
      }

    }, 3000);
    return () => {
      clearInterval(interval);
    }
  }, [pool, networkManager]);

  

  const showApr = (): Maybe<string> => {
    if (!props.pool || !props.pool.apr) {
      return "-";
    }
    if (props.pool.apr.eq(new BigNumber(0))){
      return "-";
    }
    return props.pool.apr.toFormat(2) + '%';
  }

  const showTotalStaked = (): string => {
    if (!props.pool) {
      return "0";
    }
    return ViteUtil.formatBigNumber(props.pool.totalStaked, props.pool.stakingToken.decimals, 2);
  }

  const showTotalRewards = (): string => {
    if (!props.pool) {
      return "0";
    }
    return ViteUtil.formatBigNumber(props.pool.totalRewards, props.pool.rewardToken.decimals, 2);
  }

  const showTotalInt = () => {
    var str =showTotalRewards();
    str = str.replace(/,/g, "");
    return parseFloat(str);
  }

  const showRewardTokens = (decimals: number): string => {
    if (!pool) {
      return "0";
    }
    return ViteUtil.formatBigNumber(newRewards, pool.rewardToken.decimals, decimals);
  }

  const showNetInt = (decimals: number) => {
    var str = showRewardTokens(decimals);
    str = str.replace(/,/g, "");
    return parseFloat(str);
  }

  const showPercentage = (decimals: number) => {
    return Math.round(10*100*showNetInt(decimals) / showTotalInt())/10;
  }

  const showStaked = (decimals: number): string => {
    if (!props.pool?.userInfo) {
      return "0";
    }
    return ViteUtil.formatBigNumber(props.pool.userInfo.stakingBalance, props.pool.stakingToken.decimals, decimals);
  }

  const handleConnectWallet = () => {
    emitter.emitConnectWalletDialog(true);
  }

  const handleClickDeposit = () => {
    setDialogState({
      ...dialogState,
      type: PoolDialogType.DEPOSIT,
      open: true
    })
  }

  const handleClickWithdraw = () => {
    setDialogState({
      ...dialogState,
      type: PoolDialogType.WITHDRAW,
      open: true
    })
  }

  const handleClickClaimAsync = async () => {
    try {
      if (props.pool) {
        await poolService.withdrawAsync(props.pool.id, "0");
      }
    } catch (error) {
      SnackbarUtil.enqueueError(error);
    }
  }

  return (
    <>
      <Accordion defaultExpanded>
        <AccordionSummary sx={{ backgroundColor: "rgba(217, 217, 217, 0.1)" }} expandIcon={<ExpandMoreIcon />}>
          <Grid container justifyContent="center" alignItems="center" spacing={2}>
            <Grid item>
              <Tokens loading={!props.pool} pool={props.pool}></Tokens>
            </Grid>
            <Grid item xs container alignItems="center">
              <Grid item xs container justifyContent="space-evenly" direction="row" spacing={2}>
                <Grid item>
                  {!props.pool ? (
                    <>
                      <Skeleton animation="wave" height={25} width="90px" />
                      <Skeleton animation="wave" height={25} width="70px" />
                    </>
                  ) : (
                    <>
                      <Typography variant="body2" color="text.secondary">
                        Total {props.pool?.rewardToken.originalSymbol} earned&nbsp;<Tooltip title="Total earned by all users since pool start" placement="top"><HelpIcon fontSize="small"></HelpIcon></Tooltip>
                      </Typography>
                      <Typography variant="subtitle1">
                        {showRewardTokens(3)} / {showTotalRewards()}
            
                      </Typography>
                      <ProgressBar variant="success" now={showPercentage(4)} />
                      <Typography variant="body2" color="text.secondary">            
                        {showPercentage(2)}% earned    
                      </Typography>
                    </>
                  )}
                </Grid>
                <Grid item>
                  <Typography variant="body2" color="text.secondary">
                    APR&nbsp;<Tooltip title="Annual Percentage Rate - APR is only shown for coins with a known value" placement="top"><HelpIcon fontSize="small"></HelpIcon></Tooltip>
                  </Typography>
                  {!props.pool ? (
                    <Skeleton animation="wave" height={25} width="60px" />
                  ) : (
                    <Typography variant="subtitle1">
                      {props.pool.apr ? (
                        <>
                          {showApr()}
                        </>
                      ) : (
                        <>
                          -
                        </>
                      )}
                    </Typography>
                  )}
                </Grid>
                <Grid item>
                  <Typography variant="body2" color="text.secondary">
                    Total {props.pool?.stakingToken.originalSymbol} staked&nbsp;<Tooltip title="Total staked by all users" placement="top"><HelpIcon fontSize="small"></HelpIcon></Tooltip>
                  </Typography>
                  {!props.pool ? (
                    <Skeleton animation="wave" height={25} width="120px" />
                  ) : (
                    <div>
                    <Typography variant="subtitle1">
                      {showTotalStaked()}
                    </Typography>
                    
                    </div>
                  
                  )}
                </Grid>
                <Grid item>
                  <PoolCountdown pool={props.pool} />
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </AccordionSummary>
        { network?.name === "MAINNET v0.2" ? (<Alert severity="error">For the safety of your assets, unstake from these pools and use v0.3 pools.</Alert>) : null }
        { network?.name === "MAINNET v0.4" && props?.pool?.id === 3 ? (<Alert severity="warning">This pool is broken and wont provide rewards, <strong>UNSTAKE</strong> and use the other EARN BTC pool! See Discord for more info.</Alert>) : null }
        { network?.name === "MAINNET v0.4" && props?.pool?.id === 8 ? (<Alert severity="success">Use this <strong>NEW</strong> pool for BTC rewards!</Alert>) : null }
        
        { network?.name === "MAINNET v0.4" && props?.pool?.id === 20 ? (<Alert severity="info">This is a <strong>LOCKED</strong> pool. Deposits are locked for <strong>24 hours</strong> after depositing.</Alert>) : null }
        
        { network?.name === "MAINNET v0.4" && props?.pool?.id === 29 ? (<Alert severity="info">This pool has a deposit <strong>MINIMUM</strong> of 1 million Viva.</Alert>) : null }           
        
        { network?.name === "MAINNET v0.4" && props?.pool?.id === 30 ? (<Alert severity="info">This pool has a deposit <strong>MINIMUM</strong> of 500,000 Viva.</Alert>) : null }   
        
        { network?.name === "MAINNET v0.4" && props?.pool?.id === 59 ? (<Alert severity="info">This pool has a deposit <strong>MINIMUM</strong> of 2,000 Viva.</Alert>) : null } 
        
        { network?.name === "MAINNET v0.4" && props?.pool?.id === 65 ? (<Alert severity="info">This pool has a deposit <strong>MINIMUM</strong> of 2,000 Viva.</Alert>) : null }   
        
        { network?.name === "MAINNET v0.4" && props?.pool?.id === 66 ? (<Alert severity="info">This pool has a deposit <strong>MINIMUM</strong> of 1,000,000 ViCat.</Alert>) : null }   
        
        { network?.name === "MAINNET v0.4" && props?.pool?.id === 105 ? (<Alert severity="info">This pool has a deposit <strong>MAXIMUM</strong> of 12,000,000,000,000 CENT.</Alert>) : null }
        
        { lock > 0 ? (<Alert severity="info">This is a <strong>LOCKED</strong> pool. Deposits and rewards cannot be withdrawn until the the pool ends <strong>(max {lock} days)</strong>.</Alert>) : null }
        
        <AccordionDetails sx={{ backgroundColor: "rgba(217, 217, 217, 0.3)", pt: 2 }}>
          <Grid container justifyContent="center" alignItems="center" spacing={2}>
            <Grid item sx={{ mr: 4 }}>
              <Link underline="none" href={props.pool?.rewardToken.url ?? "#"} target="_blank" sx={{ cursor: "pointer", display: "flex", alignItems: "center" }}>
                View {props.pool?.rewardToken.originalSymbol}&nbsp;<OpenInNewIcon fontSize="small"></OpenInNewIcon>
              </Link>
              <Link underline="none" href={props.pool?.stakingToken.url ?? "#"} target="_blank" sx={{ cursor: "pointer", display: "flex", alignItems: "center" }}>
                View {props.pool?.stakingToken.originalSymbol}&nbsp;<OpenInNewIcon fontSize="small"></OpenInNewIcon>
              </Link>
            </Grid>
            <Grid item xs container alignItems="center">
              <Grid item container justifyContent="space-evenly" direction="row" spacing={2}>
                <Grid item xs={12} md={6} lg={5} zeroMinWidth>
                  <TransparentPaper variant="outlined">
                    <Typography variant="body2" color="text.secondary">
                      {props.pool?.rewardToken.originalSymbol} earned since last claim
                    </Typography>
                    <Box sx={{ display: "flex", alignItems: "center" }}>
                      {!props.pool ? (
                        <Skeleton animation="wave" height={30} width="100%" />
                      ) : (
                        <Typography sx={{ width: "100%" }} noWrap>
                          <Rewards pool={props.pool} decimals={18} account={props.account} setCanClaim={setCanClaim}></Rewards>
                        </Typography>
                      )}
                      <Box sx={{ ml: 2 }} >
                        <ClickOnceButton variant="contained" size="large" callbackFn={handleClickClaimAsync} disabled={!canClaim}>
                          Claim
                        </ClickOnceButton>
                      </Box>
                    </Box>
                  </TransparentPaper>
                </Grid>
                {!props.account ? (
                  <Grid item xs={12} md={6} lg={5}>
                    <TransparentPaper variant="outlined">
                      <Typography variant="body2" color="text.secondary">
                        Start staking
                      </Typography>
                      <Button variant="contained" size="large" fullWidth disabled={!props.pool} onClick={handleConnectWallet}>Connect wallet</Button>
                    </TransparentPaper>
                  </Grid>
                ) : (
                  <>
                    <Grid item xs={12} md={6} lg={5} zeroMinWidth>
                      <TransparentPaper variant="outlined">
                        <Typography variant="body2" color="text.secondary">
                        {props.pool?.stakingToken.originalSymbol} Staked
                        </Typography>
                        <Box sx={{ display: "flex", alignItems: "center" }}>
                          {!props.pool ? (
                            <Skeleton animation="wave" height={30} width="100%" />
                          ) : (
                            <Typography sx={{ width: "100%" }} noWrap>
                              {showStaked(18)}
                            </Typography>
                          )}
                          <Tooltip
                          title="+ automatically claim pending rewards"
                          placement="bottom">
                          <Button variant="contained" size="large" sx={{ ml: 2 }} onClick={handleClickWithdraw} disabled={!canWithdraw}>
                            Withdraw
                          </Button>
                          </Tooltip>
                        </Box>
                      </TransparentPaper>
                    </Grid>
                    <Grid item xs={12} md sx={{ display: "flex", alignItems: "center" }}>
                      <Tooltip
                          title="+ automatically claim pending rewards"
                          placement="bottom">
                      <Button variant="contained" size="large" fullWidth onClick={handleClickDeposit} disabled={!canStake}>
                        Stake
                      </Button>
                      </Tooltip>
                    </Grid>
                  </>
                )}
              </Grid>
            </Grid>
          </Grid>
        </AccordionDetails>
      </Accordion>
      {props.pool && (
        <PoolDialog pool={props.pool} state={dialogState} setState={setDialogState}></PoolDialog>
      )}
    </>
  );
}
