import BigNumber from "bignumber.js";
import axios from 'axios';

type BaseResult<T> = {
  code: number,
  data: T
}

export type TokenLinks = {
  website: string
}

export type TokenDetailResult = {
  name: string
  symbol: string
  originalSymbol: string
  tokenDecimals: number
  urlIcon: string
  links: TokenLinks
}

export type TokenPriceResult = Array<TokenPriceEntry>;

export type TokenPriceEntry = {
  tokenId: string
  tokenSymbol: string
  usdRate: number
  cnyRate: number
  rubRate: number
  krwRate: number
  tryRate: number
  vndRate: number
  eurRate: number
  gbpRate: number
  inrRate: number
  btcRate: number
}


export class VitexClient {
  private readonly _baseUrl_v1: string;
  private readonly _baseUrl_v2: string;
  private readonly _baseUrl_v3: string;

  constructor() {
    this._baseUrl_v1 = "https://vitex.vite.net"
    this._baseUrl_v2 = "https://api.vitex.net"
    this._baseUrl_v3 = "https://vite-api.thomiz.dev/crypto-info/tokens/"
  }

  async getTokenDetailAsync(tokenId: string): Promise<Maybe<TokenDetailResult>> {
    const result = await axios.get<BaseResult<TokenDetailResult>>(this._baseUrl_v1 + "/api/v1/token/detail?tokenId=" + tokenId);
    //const result = await axios.get<BaseResult<TokenDetailResult>>(this._baseUrl_v3 + tokenId + "/details");
    return result.data.data;
  }

  async getTokenPriceAsync(tokenId: string): Promise<TokenPriceResult> {
    const result = await axios.get<BaseResult<TokenPriceResult>>(this._baseUrl_v2 + "/api/v2/exchange-rate?tokenIds=" + tokenId);
    return result.data.data
  }

  async getTokenPriceUSDAsync(tokenId: string): Promise<BigNumber> {
    try {
      const result = await this.getTokenPriceAsync(tokenId)
      return new BigNumber(result[0].usdRate);
    } catch (error) {
//      logger.error(error)();
      return new BigNumber(0);
    }
  }  

}

const client = new VitexClient();

export const getVitexClient = () => {
  return client;
}
